import {
  ApiType,
  DataUtil,
  DriveProvider,
  MediaProvider,
  DriveSearchResult,
  UploadFileMetadata,
  UploadInstructionSet,
  UploadResult,
  SecurityGroupType,
} from '@youfoundation/transit-lib';
import { OwnerProviderBase } from '../OwnerProviderBase';
import { ContactConfig, ContactFile, RawContact } from './ContactTypes';

//Handles management of Contacts
export class ContactProvider extends OwnerProviderBase {
  private static instance: ContactProvider;
  private _driveProvider: DriveProvider;
  private _mediaProvider: MediaProvider;

  constructor(sharedSecret: Uint8Array | undefined) {
    if (!sharedSecret) {
      throw 'Shared Secret is required';
    }
    super(sharedSecret);

    this._driveProvider = new DriveProvider({ api: ApiType.Owner, sharedSecret: sharedSecret });
    this._mediaProvider = new MediaProvider({
      api: ApiType.Owner,
      sharedSecret: sharedSecret,
      driveProvider: this._driveProvider,
    });
  }

  async ensureConfiguration() {
    await this._driveProvider.EnsureDrive(
      ContactConfig.ContactTargetDrive,
      'Contact drive',
      '',
      false
    );
  }

  async saveContact(contact: RawContact): Promise<ContactFile> {
    if (contact.id) {
      contact.fileId = (await this.getContactByTag(contact.id)).fileId;
    } else {
      const existingContact = await this.getContactByTag(contact.dotYouId);

      contact.id = existingContact?.id ?? DataUtil.getNewId();
      contact.fileId = existingContact?.fileId ?? undefined;
    }

    // Save raw image:
    if (contact.image?.content) {
      contact.imageFileId = await this._mediaProvider.uploadImage(
        ContactConfig.ContactTargetDrive,
        undefined,
        { requiredSecurityGroup: SecurityGroupType.Owner },
        DataUtil.base64ToUint8Array(contact.image.content),
        contact.imageFileId
      );
      contact.image = undefined;
    }

    const encrypt = true;

    const instructionSet: UploadInstructionSet = {
      transferIv: this._driveProvider.Random16(),
      storageOptions: {
        overwriteFileId: contact?.fileId ?? '',
        drive: ContactConfig.ContactTargetDrive,
      },
      transitOptions: null,
    };

    const payloadJson: string = DataUtil.JsonStringify64(contact as ContactFile);
    const payloadBytes = DataUtil.stringToUint8Array(payloadJson);

    const shouldEmbedContent = payloadBytes.length < 3000;
    const metadata: UploadFileMetadata = {
      contentType: 'application/json',
      appData: {
        tags: [contact.id, contact.dotYouId],
        fileType: ContactConfig.ContactFileType,
        contentIsComplete: shouldEmbedContent,
        jsonContent: shouldEmbedContent ? payloadJson : null,
      },
      payloadIsEncrypted: encrypt,
    };

    const result: UploadResult = await this._driveProvider.Upload(
      instructionSet,
      metadata,
      payloadBytes,
      undefined,
      encrypt
    );

    //update server-side info
    contact.fileId = result.file.fileId;
    return contact;
  }

  async getContactByTag(tag: string): Promise<ContactFile> {
    const response = await this._driveProvider.QueryBatch({
      targetDrive: ContactConfig.ContactTargetDrive,
      tagsMatchAtLeastOne: [tag],
    });

    if (response.searchResults.length == 0) {
      return;
    }

    if (response.searchResults.length > 1) {
      console.warn('Tag [' + tag + '] in contacts has more than one file. Using latest');
    }

    const dsr: DriveSearchResult = response.searchResults[0];
    const fileId = dsr.fileMetadata.file.fileId;

    let contact: ContactFile = { source: 'user' };

    const keyheader = dsr.fileMetadata.payloadIsEncrypted
      ? await this._driveProvider.DecryptKeyHeader(dsr.sharedSecretEncryptedKeyHeader)
      : undefined;

    if (dsr.fileMetadata.appData.contentIsComplete && response.includeMetadataHeader) {
      contact = await this._driveProvider.DecryptJsonContent<ContactFile>(
        dsr.fileMetadata,
        keyheader
      );
    } else {
      contact = await this._driveProvider.GetPayloadAsJson<ContactFile>(
        ContactConfig.ContactTargetDrive,
        fileId,
        keyheader
      );
    }

    // Set fileId for future replace
    contact.fileId = dsr.fileMetadata.file.fileId;

    return contact;
  }

  async getContacts(): Promise<ContactFile[]> {
    const response = await this._driveProvider.QueryBatch({
      targetDrive: ContactConfig.ContactTargetDrive,
      fileType: [ContactConfig.ContactFileType],
    });

    if (response.searchResults.length == 0) {
      return [];
    }

    return await Promise.all(
      response.searchResults.map(async (result) => {
        const dsr: DriveSearchResult = result;
        const fileId = dsr.fileMetadata.file.fileId;

        let contact: ContactFile = { source: 'user' };

        const keyheader = dsr.fileMetadata.payloadIsEncrypted
          ? await this._driveProvider.DecryptKeyHeader(dsr.sharedSecretEncryptedKeyHeader)
          : undefined;
        if (dsr.fileMetadata.appData.contentIsComplete && response.includeMetadataHeader) {
          contact = await this._driveProvider.DecryptJsonContent<ContactFile>(
            dsr.fileMetadata,
            keyheader
          );
        } else {
          contact = await this._driveProvider.GetPayloadAsJson<ContactFile>(
            ContactConfig.ContactTargetDrive,
            fileId,
            keyheader
          );
        }

        // Set fileId for future replace
        contact.fileId = dsr.fileMetadata.file.fileId;

        return contact;
      })
    );
  }

  public static getInstance(sharedSecret): ContactProvider {
    if (!ContactProvider.instance) {
      ContactProvider.instance = new ContactProvider(sharedSecret);
    }

    return ContactProvider.instance;
  }
}
